<template>
  <div class="con">
    <div class="qv-head">
      <img src="@/assets/guan-close.png" @click="goBack" />
      <div class="qv-head-text">筛选</div>
    </div>
    <div class="select-all">
      <div class="select-title">单位性质</div>
      <div class="select-list">
        <div
          v-for="(item, index) in list"
          :key="index"
          class="select-list-title"
          :class="listIndex == index ? 'actived' : ''"
          @click="goTab(index, item.value)"
        >
          {{ item.label }}
        </div>
      </div>
      <div class="select-title">所属行业</div>
      <div class="select-list">
        <div
          v-for="(item, index) in list1"
          :key="index"
          class="select-list-title"
          :class="listIndex1 == index ? 'actived' : ''"
          @click="goTab1(index, item.value)"
        >
          {{ item.label }}
        </div>
      </div>
    </div>
    <div class="select-footer">
      <div class="select-footer-l" @click="goAgain">重置</div>
      <div class="select-footer-r" @click="goConfirm">确定</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "zhaoMoreSelect",
  data() {
    return {
      list: [],
      listIndex: 999999,
      list1: [],
      listIndex1: 9999999,
    };
  },
  methods: {
    goTab(index, value) {
      console.log(index, value);
      this.listIndex = index;
      this.value = value;
    },
    goTab1(index, value) {
      console.log(index, value);
      this.listIndex1 = index;
      this.value1 = value;
    },
    goBack() {
      this.$router.go(-1);
    },
    goAgain() {
      this.listIndex = 999999;
      this.listIndex1 = 9999999;
      // this.$store.commit("updataValue", "");
      // this.$store.commit("updataValue1", "");
      sessionStorage.setItem("Value", "");
      sessionStorage.setItem("Value1", "");
      this.$store.commit("updataIndex", "");
      this.$store.commit("updataIndex1", "");
      this.$router.go(0);
    },
    goConfirm() {
      this.$store.commit("updataIndex", this.listIndex);
      this.$store.commit("updataIndex1", this.listIndex1);
      // this.$store.commit("updataValue", this.value);
      // this.$store.commit("updataValue1", this.value1);
      sessionStorage.setItem("Value", this.value);
      sessionStorage.setItem("Value1", this.value1);
      this.$router.go(-1);
    },

    getData() {
      const params = {
        positionType: 1,
      };
      console.log(params);
      this.$axios
        .post("/f/recruitmentinfo/ajax_search", params)
        .then((res) => {
          console.log(res, "数据");
          if (res.data.state == 1) {
            if (res.data.object.corporationNature) {
              this.list = res.data.object.corporationNature;
            }
            if (res.data.object.industry) {
              this.list1 = res.data.object.industry;
            }
          } else {
            this.list = [];
            this.list1 = [];
          }
        })
        .catch((err) => {
          console.log("获取失败");
        });
    },
  },

  created() {
    console.log(this.$store.getters.getIndex, "89");
    console.log(this.$store.getters.getIndex1, "89");
    console.log(sessionStorage.getItem("Value"), "89");
    console.log(sessionStorage.getItem("Value1"), "89");
    // this.listIndex = this.$store.getters.getIndex;
    // this.listIndex1 = this.$store.getters.getIndex1;

    if (this.$store.getters.getIndex) {
      this.listIndex = this.$store.getters.getIndex;
      console.log(this.listIndex);
    } else {
      this.listIndex = 99999999;
    }

    if (this.$store.getters.getIndex1) {
      this.listIndex1 = this.$store.getters.getIndex1;
      console.log(this.listIndex1);
    } else {
      this.listIndex1 = 99999999;
    }

    this.value = sessionStorage.getItem("Value");
    this.value1 = sessionStorage.getItem("Value1");
    this.getData();
  },
  beforeDestory() {},
  destroyed() {},
};
</script>
<style lang="css" scoped>
.con {
  background-color: #f5f5f5;
  padding-bottom: 1.66rem;
  min-height: 100%;
}
.qv-head {
  width: 7.5rem;
  height: 0.88rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #ffffff;
}
.qv-head img {
  position: absolute;
  left: 0.52rem;
  width: 0.32rem;
  height: 0.32rem;
  top: 0.28rem;
}
.qv-head-text {
  font-family: PingFangSC-Semibold;
  font-size: 0.32rem !important;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #2b2e34;
}
.select-all {
  width: 7.5rem;
  background-color: rgba(255, 255, 255, 0.7);
  border-top: 0.01rem solid #eeeeee;
  padding: 0.38rem 0.32rem;
  box-sizing: border-box;
}

.select-title {
  width: 6.86rem;
  margin-bottom: 0.32rem;
  font-family: PingFangSC-Semibold;
  font-size: 0.3rem;
  font-weight: bold;
  font-stretch: normal;
  letter-spacing: 0.01rem;
  color: #373d41;
  text-align: left;
}

.select-list {
  width: 6.86rem;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding-bottom: 0.26rem;
}

.select-list-title {
  width: 2.08rem;
  height: 0.7rem;
  background-color: #f5f6f9;
  border-radius: 0.04rem;
  margin-bottom: 0.3rem;
  text-align: center;
  line-height: 0.7rem !important;
  font-family: PingFangSC-Regular;
  font-size: 0.26rem;
  font-weight: normal;
  font-stretch: normal;
  line-height: 0.42rem;
  letter-spacing: 0rem;
  color: #7a7c8b;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  margin-right: 0.3rem;
  word-break: break-all;
}

.select-list-title:nth-child(3n + 3) {
  margin-right: 0 !important;
}

.actived {
  width: 2.08rem;
  height: 0.7rem;
  background-color: #f7f9ff;
  border-radius: 0.04rem;
  margin-bottom: 0.3rem;
  text-align: center;
  line-height: 0.7rem !important;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  font-family: PingFangSC-Regular;
  font-size: 0.26rem;
  font-weight: bold;
  font-stretch: normal;
  line-height: 0.42rem;
  letter-spacing: 0rem;
  color: #4760d7;
  margin-right: 0.31rem;
  word-break: break-all;
}

.actived:nth-child(3n) {
  margin-right: 0rem !important;
}

.select-footer {
  width: 7.5rem;
  height: 2rem;
  background-color: rgba(255, 255, 255);
  padding: 0.36rem 0.32rem 0.62rem 0.32rem;
  box-sizing: border-box;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 99;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.select-footer-l {
  width: 2.72rem;
  height: 0.8rem;
  border: 0.02rem #4760d7 solid;
  text-align: center;
  line-height: 0.8rem;
  font-family: PingFangSC-Regular;
  font-size: 0.28rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #4760d7;
  border-radius: 0.08rem;
}

.select-footer-r {
  width: 3.96rem;
  height: 0.8rem;
  background-color: #4760d7;
  border-radius: 0.08rem;
  text-align: center;
  line-height: 0.8rem;
  font-family: PingFangSC-Regular;
  font-size: 0.28rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #ffffff;
}
</style>


